*{
  font-family: 'Tenor Sans', sans-serif;
}
main {
  margin-top: 175px;
  margin-bottom:100px;
}

.App {
  text-align: center;
 
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.image {
  height:auto;
  min-height: 30vh;
  max-height: 80vh;
  width:100%;
  max-width: 750px;
  text-align: center;
  overflow-x: inherit;
  -webkit-box-shadow: 0px 10px 13px -7px #000000, -1px 2px 13px 7px rgba(0,0,0,0.34); 
box-shadow: 0px 10px 13px -7px #000000, -1px 2px 13px 7px rgba(0,0,0,0.34);
  
}
.card-body{
  width:100%;
  background: aliceblue;
}
.link-title{
  text-align: center;
}

.project-card{
  margin-bottom:10px;
  width: 100% !important;
  border-style: ridge !important;
  border-color: cornflowerblue !important;
  border-width:2.5px !important;
}

.col-container {
  text-align: -webkit-center;
}
.myName{
  font-size: xxx-large !important;
}
.footer-container {
  justify-content: center !important;
}
.form-labels {
  font-size: large;
  text-decoration: underline;
  text-decoration-color: cornflowerblue;
}
.error {
  color:red
}
.submitButton {
  text-align: center;
  margin-top:10px;
}
#resume-container {
  width: 100%;
  height:100%;
  min-height: 500px;
  text-align: center;
}
#resume-iframe {
  width: 90%;
  height:100%;
  min-height: 700px;
}
#aboutContainer{
  margin-top: 25px;
}
#aboutImage,#portrait, .navbar, #aboutCard, #formContainer {
  border-style: ridge;
  border-color: cornflowerblue;
}
#headerNav {
  border-width: 7.5px;
  border-top:none;
  border-right:none;
  border-left:none;
}
#footerNav {
  border-width: 7.5px;
  border-bottom:none;
  border-right:none;
  border-left:none;
}
#aboutCard {
  padding: 15px;
  border-width: 5px;
  background: aliceblue;
}
#aboutImage {
  height:auto;
  width: 100%;
  border-style: outset;
  border-color: cornflowerblue;
}
#aboutTitle {
  margin:5px;
  text-align: center;
  font-size: x-large;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  text-decoration: underline;
  text-decoration-color: cornflowerblue;
}

#aboutText{
  align-self: center;
}
#mission {
  text-align: center !important;
  font-size: x-large !important;
  font-weight:bolder;
}
#portfolioContainer {
  width: 95%;
}
#portrait {
  height:auto;
  width:100%;
  max-width:100px;
  margin-right:15px
}
#formContainer {
  padding:25px;
  background: aliceblue;
  border-radius: 5px;
}
.buttonContainer {
  text-align: center;
}
.card-header, .card-footer {
  background-color:rgb(235, 235, 235) !important;
  border-style: ridge !important;
  border-color: cornflowerblue !important;
  border-width: 2.5px !important;
  border-top:none !important;
  border-left:none !important;
  border-right: none !important;
}
.card-title {
  font-size: xx-large !important;
}
#returnMessage {
  text-align: center;
  font-size: xx-large;
}
@media screen and (max-width: 990px){
  .tocContainer {
    visibility: hidden;
  }
#portfolioContainer {
    width: 100%;
    max-width: 100%;
    margin:0px 0px 0px 0px !important;
  }
  .image {
    height:auto;
    min-height: 30vh;
    max-height: 75vh;
    width: 100%;
    max-width: 750px;
    text-align: center;
  }
  .myName{
    font-size: xx-large !important;
  }
}
@media screen and (max-width: 430px){
  main {
    margin-top: 100px;
    margin-bottom:100px;
  }

  #portrait {
    max-width:50px;
  }
  .myName{
    font-size: large !important;
  }
}
@media screen and (max-width: 322px){
  .footerImg{
    height:32px;
    width: 32px;
  }
}